@import url(https://fonts.googleapis.com/css?family=Lato:100,400|Libre+Baskerville|Pacifico|Playfair+Display&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.toolbar {
    position: fixed;
    width: 100%;
    background: white;
    height: 56px;
    top: 0;
    left: 0;
}

.search-icon {
    padding-left: 5px;
    height: .8rem;
}



.toolbar__logo a {
    color: black;
    text-decoration: none;
    font-size: 1.5rem;
    font-family: Raleway;
    letter-spacing: 1.8px;
}

.toolbar__navigation-items a {
    color: black;
    text-decoration: none;
    font-size: 1rem;
    font-family: Montserrat;
    padding-bottom: 15px;
}

.toolbar__navigation {
    display: flex;
    height: 100%;
    align-items: center;
    padding: 0 1rem;
    /* border-bottom: 1.5px solid #D8E1FF;  */
    border-bottom: 1.5px solid lightgray;
}

.search:hover, 
.search:focus {
    color: #927C93;
    border: none!important;
}

.toolbar__navigation-items a:hover,
.toolbar__navigation-items a:focus {
    /* color: #fa923f;    */
    border-bottom: 3px solid #927C93;
}

.toolbar__navigation-items ul{
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
}

.toolbar__navigation-items li{
    padding: 0 0.5rem;
}

.spacer {
    flex: 1 1;
}
/* Showcase */

.showcase {
    background:url(/static/media/hero-bg2.5b8d39d2.jpg) no-repeat center;
    background-size: cover;
    height: 500px;
    color: black;
  }
  
h1 {
    text-align: center;
    padding-top: 14rem;
    font-size: 50px;
